import * as React from 'react';
import {SyntheticEvent} from "react";
import Rheostat, {Props} from 'rheostat';
import 'rheostat/css/rheostat.css';
import 'rheostat/initialize';
import geometric from './algorithms/geometric';
import './App.css';
import {default as calculateLoan, ILoan} from "./CalculateLoan";
import LoanDetails from "./LoanDetails";
import LoanSummary from "./LoanSummary";

export interface IDetails {
    loanTerm: number;
    value: number;
}

interface IAppState {
    currentValue: number;
    possibleValues: number[];
    loanSpecs: ILoanSpec[];
    calculatedLoans: ICalculatedLoans;
    selectedDetails: IDetails;
}

// TODO moved for the sake of readability; normally these would come from an API anyway
const possibleValuesData: number[] = [100, 200, 300, 400, 500, 1000, 1500, 2000, 3000, 4000, 5000];

export interface ILoanSpec {
    feeMultiplier: number,
    interest: number,
    loanTerm: number,
    maxMonthlyFee: number
}

export const loanSpecsData: ILoanSpec[] = [
    {
        feeMultiplier: 0.05,
        interest: 0.06,
        loanTerm: 3,
        maxMonthlyFee: 91.50
    },
    {
        feeMultiplier: 0.03,
        interest: 0.06,
        loanTerm: 6,
        maxMonthlyFee: 43.50
    },
    {
        feeMultiplier: 0.022,
        interest: 0.06,
        loanTerm: 12,
        maxMonthlyFee: 19.50

    },
    {
        feeMultiplier: 0.015,
        interest: 0.06,
        loanTerm: 24,
        maxMonthlyFee: 7.47
    },
    {
        feeMultiplier: 0.01,
        interest: 0.06,
        loanTerm: 60,
        maxMonthlyFee: 37.00
    },
]

export interface ICalculatedLoans {
    [amount: number]:  ILoan[];
}

class App extends React.Component<{}, IAppState> {
    constructor(props: {}) {
        super(props);
        const initialCurrentValue: number = 2000;
        const calculatedLoans = {[initialCurrentValue]: calculateLoan(initialCurrentValue, loanSpecsData)};

        this.state = {
            calculatedLoans,
            currentValue: initialCurrentValue,
            loanSpecs: loanSpecsData,
            possibleValues: possibleValuesData,
            selectedDetails: {
                loanTerm: 0,
                value: 0
            }
        };

        this.updateValue = this.updateValue.bind(this);
        this.setSelectedDetails = this.setSelectedDetails.bind(this);
        this.resetSelectedDetails = this.resetSelectedDetails.bind(this);
    }

    public render() {
        const {possibleValues, currentValue, calculatedLoans} = this.state;

        const PitComponent = (props: any) => {
            return (
                <div
                    style={{
                        ...props.style,
                        background: '#a2a2a2',
                        height: props.children % 10 === 0 ? 12 : 8,
                        top: 20,
                        width: 1,
                    }}
                />
            );
        };

        return (
            <div className="App">
                <div className="my-slider">
                    <h1>Get the loan that suits you best!</h1>
                    <div style={{maxWidth: '640px', margin: '0 auto'}}>
                        <Rheostat
                            algorithm={geometric} // TODO a custom function would be better
                            min={100}
                            max={5000}
                            onChange={this.updateValue}
                            snap={true}
                            snapPoints={possibleValues}
                            pitPoints={possibleValues}
                            pitComponent={PitComponent}
                            values={[currentValue]}
                        />
                    </div>
                    <h2 style={{textAlign: 'center'}}>Loan {currentValue}</h2>
                    <LoanSummary calculatedLoans={calculatedLoans} currentValue={currentValue} selectedDetails={this.state.selectedDetails} setSelectedDetails={this.setSelectedDetails} />
                    {
                        this.state.selectedDetails.loanTerm && this.state.selectedDetails.value ?
                        <LoanDetails calculatedLoans={calculatedLoans} selectedDetails={this.state.selectedDetails}/>
                        : null
                    }
                </div>
            </div>
        );
    }

    private updateValue(sliderProps: Props) {
        if (sliderProps && sliderProps.values) {
            this.setState((prevState) => {
                if (sliderProps && sliderProps.values) {
                    return {
                        ...prevState,
                        currentValue: sliderProps.values[0]
                    };
                }
                return {
                    ...prevState
                };
            });

            this.calculateLoan(sliderProps.values[0], this.state.loanSpecs);
            this.resetSelectedDetails();
        }
    }

    private setSelectedDetails(e: SyntheticEvent<HTMLButtonElement>) {
        const loanTerm: number = Number(e.currentTarget.getAttribute('loan-term'));
        const value: number = Number(e.currentTarget.getAttribute('loan-value'));
        this.setState((prevState) => {
            return {
                ...prevState,
                selectedDetails: {
                    loanTerm,
                    value
                }
            };
        });
    }

    private resetSelectedDetails() {
        this.setState((prevState) => {
            return {
                ...prevState,
                selectedDetails: {
                    loanTerm: 0,
                    value: 0
                }
            };
        });
    }

    private calculateLoan(loanValue: number, loanSpecs: ILoanSpec[]) {
        if (this.state.calculatedLoans[loanValue]) {
            return this.state.calculatedLoans[loanValue]      ;
        } else {
            const calculatedLoans = {
                [loanValue]: calculateLoan(loanValue, loanSpecs)
            };
            this.setState((prevState) => {
                return {
                    ...prevState,
                    calculatedLoans: {
                        ...prevState.calculatedLoans,
                        ...calculatedLoans
                    }
                };
            });
            return calculatedLoans;
        }
    }
}

export default App;



