import * as React from 'react';
import {CSSProperties, SyntheticEvent} from "react";
import {ICalculatedLoans, IDetails} from "./App";
import {ILoan} from "./CalculateLoan";
import './LoanSummary.css';

export interface ILoanSummaryProps {
    calculatedLoans: ICalculatedLoans;
    currentValue: number;
    selectedDetails: IDetails;
    setSelectedDetails:  (e: SyntheticEvent<HTMLButtonElement>) => void;
}

const wrapperStyle: CSSProperties = {
    display: 'flex',
    flexWrap: 'wrap'
};

const summaryStyle: CSSProperties = {
    flexGrow: 1,
    padding: '12px'
};

const LoanSummary = (props: ILoanSummaryProps) => {
    const loans: ILoan[] = props.calculatedLoans[props.currentValue];
    return (
        <React.Fragment>
            <h2>Available payment plans</h2>
            <div style={wrapperStyle}>
                {loans.map((loan: ILoan) => {
                    let selected: string = '';
                    if(loan.loanTerm === props.selectedDetails.loanTerm) {
                        selected = 'selected';
                    }

                    return (<div key={loan.loanTerm} style={summaryStyle} className={selected}>
                        <p>Loan term: {loan.loanTerm} months</p>
                        <p>Monthly installment: {loan.monthlyInstallment.toFixed(2)}</p>
                        <p>Total amount: {loan.totalAmount.toFixed(2)}</p>
                        <button
                            loan-term={loan.loanTerm}
                            loan-value={props.currentValue}
                            onClick={props.setSelectedDetails}>
                            See details
                        </button>
                    </div>);
                })}

            </div>
        </React.Fragment>
    );
};

export default LoanSummary;



