import * as React from 'react';
import {CSSProperties} from "react";
import {ICalculatedLoans, IDetails} from "./App";
import {ILoan, IpaymentPlanEntry} from "./CalculateLoan";
import './LoanDetails.css';

export interface ILoanDetailsProps {
    calculatedLoans: ICalculatedLoans;
    selectedDetails: IDetails;
}

const tableStyle: CSSProperties = {
    border: '1px solid #777',
    borderCollapse: 'collapse',
};

const tableHeaderStyle: CSSProperties = {
    background: '#eee',
};

const LoanDetails = (props: ILoanDetailsProps) => {
    const matchingLoan: ILoan | undefined = props.calculatedLoans[props.selectedDetails.value].find((loan: ILoan) => {
        return loan.loanTerm === props.selectedDetails.loanTerm;
    });
    const paymentPlan: IpaymentPlanEntry[] | null = matchingLoan ? matchingLoan.paymentPlan : null;

    return (
        paymentPlan ?
            <React.Fragment>
                <h2>Detailed breakdown</h2>
                <table style={tableStyle}>
                    <thead style={tableHeaderStyle}>
                        <tr>
                            <th>#</th>
                            <th>Capital left</th>
                            <th>Interest</th>
                            <th>Fees</th>
                        </tr>
                    </thead>
                    <tbody>
                        {paymentPlan.map((plan: IpaymentPlanEntry) =>
                            <tr key={plan.month}>
                                <td>{plan.month}</td>
                                <td>{plan.capitalLeft.toFixed(2)}</td>
                                <td>{plan.interestThisMonth.toFixed(2)}</td>
                                <td>{plan.feesThisMonth.toFixed(2)}</td>
                            </tr>
                        )}
                    </tbody>
                </table>
            </React.Fragment>
            : null
    );
};

export default LoanDetails;



