import Decimal from 'decimal.js';
import {ILoanSpec} from './App';

export interface IpaymentPlanEntry {
    capitalLeft: number;
    feesThisMonth: number;
    interestThisMonth: number;
    month: number;
}

export interface ILoan {
    loanTerm: number;
    monthlyInstallment: number;
    paymentPlan: IpaymentPlanEntry[];
    totalAmount: number;
}

function calculateLoan(loanValue: number, loanSpecs: ILoanSpec[]): ILoan[] {
    const loans: ILoan[] = [];

    return loanSpecs.reduce((acc, loanSpec, index, array): ILoan[] => {
        const {feeMultiplier, interest, loanTerm, maxMonthlyFee} = loanSpec;

        const calculatedMonthlyFee: number = computeCalculatedMonthlyFee(loanValue, feeMultiplier, maxMonthlyFee);

        const realInterestRateSliced: number = Number(new Decimal(interest).mul(360).div(365).toDecimalPlaces(6));

        const periodicInterestRate: number = Number(new Decimal(realInterestRateSliced).div(12));

        const negativePmt: Decimal = computeNegativePmt(loanValue, periodicInterestRate, loanTerm);

        const monthlyInstallment: number = computeMonthlyInstallment(negativePmt, calculatedMonthlyFee);

        if (isLoanValid(loanValue, loanTerm, monthlyInstallment)) {
            const paymentPlan: IpaymentPlanEntry[] = computePaymentPlans(loanValue, loanTerm, monthlyInstallment, interest, calculatedMonthlyFee)

            const totalAmount: number = Number(new Decimal(
                paymentPlan.reduce((total, paymentPlanEntry) => {
                    return total + paymentPlanEntry.feesThisMonth;
                }, 0)
            ));

            acc.push({
                loanTerm,
                monthlyInstallment,
                paymentPlan,
                totalAmount
            });
        }
        return acc;
    }, loans);
}

/*export*/ function computeCalculatedMonthlyFee(loanValue: number, feeMultiplier: number, maxMonthlyFee: number): number {
    return Number(new Decimal(loanValue).mul(feeMultiplier).toDecimalPlaces(2)) > Number(new Decimal(maxMonthlyFee).toDecimalPlaces(2)) ? Number(new Decimal(maxMonthlyFee).toDecimalPlaces(2)) : Number(new Decimal(loanValue).mul(feeMultiplier).toDecimalPlaces(2));
}

/*export*/ function computeNegativePmt(loanValue: number, periodicInterestRate: number, loanTerm: number) {
    const top: Decimal = new Decimal(loanValue).mul(periodicInterestRate);
    const subtractFrom: Decimal = new Decimal(1);
    const exponent: Decimal = new Decimal(loanTerm).negated();
    const subtractThis: Decimal = new Decimal(new Decimal(1).add(periodicInterestRate)).pow(exponent);
    const bottom: Decimal = subtractFrom.sub(subtractThis);
    const negativePmt: Decimal = top.div(bottom);
    return negativePmt.toDecimalPlaces(4);
}

/*export*/ function computeMonthlyInstallment(negativePmt: Decimal, calculatedMonthlyFee: number) {
    return  Number(negativePmt.add(new Decimal(calculatedMonthlyFee)).mul(100).ceil().div(100).toDecimalPlaces(2));
}

/*export*/ function computePaymentPlans(loanValue: number, loanTerm: number, monthlyInstallment: number, interest: number, calculatedMonthlyFee: number) {
    const paymentPlans: IpaymentPlanEntry[] = [];
    let capitalLeft: number = loanValue;
    let interestThisMonth: number = capitalLeft*interest/365*30;
    let feesThisMonth: number = monthlyInstallment;

    for (let i = 0; i < loanTerm; i++) {
        if (i > 0) {
            capitalLeft = capitalLeft-(monthlyInstallment-interestThisMonth-calculatedMonthlyFee);
        }

        interestThisMonth = capitalLeft*interest/365*30;

        if (i === loanTerm-1) {
            feesThisMonth = capitalLeft + interestThisMonth + calculatedMonthlyFee;
        }

        paymentPlans.push({
            capitalLeft,
            feesThisMonth,
            interestThisMonth,
            month: i+1
        });
    }
    return paymentPlans;
}

/*export*/ function isLoanValid(loanValue: number, loanTerm: number, monthlyInstallment: number): boolean {
    if (loanTerm === 60 && loanValue <= 2000) {
        return false;
    }
    if (monthlyInstallment < 5) {
        return false;
    }
    return true;
}

export default calculateLoan;


